/* ----------------------------------- COLORS ----------------------------------- */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  font-size: 18px;
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  color: hsl(208deg, 11%, 55%);
}

body {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
}

.container {
  width: calc(100% - 30px);
  margin: 0 auto;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  color: hsl(192deg, 100%, 9%);
}

h1 {
  font-size: 1.35rem;
}

h2 {
  font-size: 1.15rem;
}

h3 {
  font-size: 1rem;
  margin-bottom: 1.2rem;
}

p {
  font-size: 0.8rem;
  line-height: 1.2rem;
}

.btn {
  text-decoration: none;
  text-align: center;
  display: block;
  border-radius: 10rem;
  width: fit-content;
  box-shadow: 0 1px 5px 0px hsla(0deg, 0%, 0%, 0.1);
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
  transition: background-color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
  cursor: pointer;
}
.btn:hover {
  box-shadow: 0 1px 8px 0px hsla(0deg, 0%, 0%, 0.2);
}

.btn-primary, .btn-call-to-action {
  color: hsl(193deg, 100%, 96%);
  background-color: hsl(322deg, 100%, 66%);
  padding: 0.65rem 0;
  width: 100%;
  letter-spacing: 0.03rem;
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
  margin: 0 auto;
}
.btn-primary:hover, .btn-call-to-action:hover {
  background-color: #ff85d2;
}

.btn-basic {
  color: hsl(192deg, 100%, 9%);
  background-color: hsl(0deg, 0%, 100%);
  padding: 0.3rem 1.5rem;
  font-size: 0.6rem;
}
.btn-basic:hover {
  color: rgba(0, 37, 46, 0.7);
}

.btn-primary {
  max-width: 240px;
  font-size: 0.6rem;
}

.btn-call-to-action {
  max-width: 200px;
  font-size: 0.55rem;
}

header {
  background-image: url(../img/bg-hero-mobile.svg);
  background-color: hsl(193deg, 100%, 96%);
}
header .header-container {
  padding: 1.3rem 0 0.4rem;
}
header .header-container .header-container__top-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 3rem;
}
header .header-container .header-container__top-wrapper img {
  max-width: 105px;
  object-fit: contain;
}
header .header-container .header-container__bottom-wrapper {
  padding: 1.2rem;
}
header .header-container .header-container__bottom-wrapper .header-description-container {
  display: flex;
  flex-direction: column;
  margin-bottom: 2.8rem;
}
header .header-container .header-container__bottom-wrapper .header-description-container h1 {
  margin-bottom: 1.2rem;
}
header .header-container .header-container__bottom-wrapper .header-description-container p {
  color: hsl(192deg, 100%, 9%);
  font-size: 0.88rem;
  margin-bottom: 2rem;
  line-height: 1.3rem;
}
header .header-container .header-container__bottom-wrapper img {
  width: 100%;
  max-width: 600px;
  object-fit: contain;
}

main {
  background-color: hsl(0deg, 0%, 99%);
  padding-top: 3rem;
  padding-bottom: 11rem;
  position: relative;
}
main .main-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
main .main-container section {
  padding: 3rem 2rem 2rem;
  box-shadow: 0 0 8px 0px hsla(0deg, 0%, 0%, 0.05);
  border-radius: 1rem;
  display: flex;
  flex-direction: column-reverse;
  background-color: hsl(0deg, 0%, 100%);
}
main .main-container section .section-description-container h2 {
  margin-bottom: 1rem;
}
main .main-container section .illustration {
  width: 90%;
  max-width: 600px;
  aspect-ratio: 1/1;
  background-repeat: no-repeat;
  background-size: 100%;
  margin: 0 auto;
}
main .main-container #section1 .illustration {
  background-image: url(../img/illustration-grow-together.svg);
}
main .main-container #section2 .illustration {
  background-image: url(../img/illustration-flowing-conversation.svg);
}
main .main-container #section3 .illustration {
  background-image: url(../img/illustration-your-users.svg);
}
main .main-container #call-to-action {
  width: calc(100% - 30px);
  position: absolute;
  bottom: 0;
  transform: translateY(35%);
  flex-direction: column;
  padding: 2rem 0.5rem 2rem;
}

footer {
  background-color: hsl(192deg, 100%, 9%);
  text-align: left;
  color: hsl(193deg, 100%, 96%);
}
footer .footer-container {
  margin-top: 7.5rem;
  margin-bottom: 2.5rem;
}
footer .footer-container .logo {
  max-width: 170px;
  margin-bottom: 1.2rem;
}
footer .footer-container .links-container {
  display: flex;
  flex-direction: column;
}
footer .footer-container .links-container .contacts {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 3.3rem;
}
footer .footer-container .links-container .contacts li {
  display: grid;
  grid-template-columns: 2.5rem 1fr;
}
footer .footer-container .links-container .contacts li img {
  padding-top: 0.2rem;
}
footer .footer-container .links-container .contacts li p {
  font-size: 0.9rem;
  line-height: 1.4rem;
}
footer .footer-container .links-container .internal-links {
  margin-bottom: 2rem;
}
footer .footer-container .links-container .internal-links a {
  display: block;
  width: fit-content;
  text-decoration: underline rgba(235, 251, 255, 0);
  color: hsl(193deg, 100%, 96%);
  margin-bottom: 1rem;
  transition: text-decoration 0.3s ease-in-out;
}
footer .footer-container .links-container .internal-links a:hover {
  text-decoration: underline #ebfbff;
}
footer .footer-container .links-container .social-links {
  display: flex;
  justify-content: center;
  gap: 0.8rem;
  margin-bottom: 1.4rem;
}
footer .footer-container .links-container .social-links i {
  color: hsl(193deg, 100%, 96%);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 1.8rem;
  height: 1.8rem;
  font-size: 0.9rem;
  border-radius: 50%;
  border: 1px solid hsl(193deg, 100%, 96%);
  cursor: pointer;
  transition: border-color 0.2s ease-in-out, color 0.2s ease-in-out;
}
footer .footer-container .links-container .social-links i:hover {
  color: #ff85d2;
  border-color: #ff85d2;
}
footer .footer-container .copyright {
  text-align: center;
  font-size: 0.65rem;
}

@media screen and (min-width: 768px) {
  body {
    text-align: left;
  }
  .container {
    max-width: 1240px;
  }
  /* TEXT */
  h1 {
    font-size: 1.8rem;
  }
  h2 {
    font-size: 1.5rem;
  }
  h3 {
    font-size: 1.75rem;
  }
  p {
    font-size: 1rem;
  }
  /* BTN */
  .btn-primary, .btn-call-to-action {
    padding: 1rem 0;
    letter-spacing: 0.03rem;
    margin: 0;
  }
  .btn-basic {
    padding: 0.7rem 3.5rem;
    font-size: 0.8rem;
  }
  .btn-primary {
    max-width: 280px;
    font-size: 0.75rem;
  }
  .btn-call-to-action {
    max-width: 400px;
    font-size: 1.05rem;
  }
  /* HEADER */
  header {
    background-image: url(../img/bg-hero-desktop.svg);
  }
  header .header-container {
    padding: 2.8rem 0 3.8rem;
  }
  header .header-container .header-container__top-wrapper {
    margin-bottom: 4.2rem;
  }
  header .header-container .header-container__top-wrapper img {
    max-width: 200px;
  }
  header .header-container .header-container__bottom-wrapper {
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 2rem;
  }
  header .header-container .header-container__bottom-wrapper .header-description-container {
    margin-bottom: 0;
    max-width: 510px;
  }
  header .header-container .header-container__bottom-wrapper .header-description-container h1 {
    margin-bottom: 1.5rem;
  }
  header .header-container .header-container__bottom-wrapper .header-description-container p {
    font-size: 1rem;
    margin-bottom: 2rem;
    line-height: 1.5rem;
  }
  header .header-container .header-container__bottom-wrapper img {
    width: 40%;
    max-width: 680px;
  }
  /* MAIN */
  main {
    padding-top: 8.5rem;
    padding-bottom: 19rem;
  }
  main .main-container {
    align-items: center;
    gap: 2.5rem;
  }
  main .main-container section {
    width: 100%;
    padding: 3rem 2rem 3rem 6.5rem;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    gap: 1rem;
  }
  main .main-container section .section-description-container {
    flex: 6;
    max-width: 500px;
  }
  main .main-container section .section-description-container h2 {
    margin-bottom: 1rem;
  }
  main .main-container section .illustration {
    flex: 4;
    width: 100%;
    max-width: 420px;
    aspect-ratio: 1/0.8;
    margin: 0;
  }
  main .main-container section:nth-child(2n) {
    flex-direction: row-reverse;
  }
  main .main-container #call-to-action {
    max-width: 800px;
    padding: 3.7rem 0.5rem 2.85rem;
    transform: translateY(30%);
  }
  /* FOOTER */
  footer {
    font-size: 0.8rem;
  }
  footer .footer-container {
    margin-bottom: 2rem;
  }
  footer .footer-container .logo {
    margin-bottom: 1.5rem;
  }
  footer .footer-container .links-container {
    flex-direction: row;
    gap: 2rem;
  }
  footer .footer-container .links-container .contacts {
    flex: 1;
    gap: 1rem;
    margin-bottom: 0;
  }
  footer .footer-container .links-container .contacts li p {
    font-size: 0.8rem;
  }
  footer .footer-container .links-container .internal-links {
    flex: 1;
    display: flex;
    justify-content: space-around;
    margin-bottom: 0;
  }
  footer .footer-container .links-container .social-links {
    flex: 1;
  }
  footer .footer-container .copyright {
    text-align: right;
    font-size: 0.65rem;
    transform: translateY(-100%);
  }
}
@media screen and (min-width: 992px) {
  h1 {
    font-size: 2.2rem;
  }
  header .header-container .header-container__bottom-wrapper img {
    width: 60%;
  }
}